import React, {Component} from 'react';


export class Add extends Component {

    state = {
        title: '',
        desc: ''
    }

onChangeTitle = (e) => {
        this.setState(
            {
                title: e.target.value,
            }
        )
}

onChangeDesc = (e) => {
        this.setState(
            {
                desc: e.target.value,
            }
        )
    }

    onSubmit = (e) => {
        e.preventDefault();
        console.log(this.state.title, this.state.desc);
        this.props.AddAnnounc(this.state.title, this.state.desc);
    }

    render() {
        return (
            <div className="Add">
                <h2>Add announcement</h2>
                <form onSubmit={this.onSubmit}>
                    <label onChange={this.onChangeTitle}>Title</label><input type='text'/><br/>
                    <label onChange={this.onChangeDesc}>Description</label><textarea/><br/>

                    <button type='submit' >Add</button>
                </form>
            </div>
        );
    }
}
